import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import { Pixelify } from "react-pixelify";
import ProcessImage from 'react-imgpro';
import ImageUploader from 'react-images-upload';
import Jimp from 'jimp/es';

var jimp = require('jimp');

var src = require("./blank-black.png");
var imgFinal = "./blank-black.png";

class PixelateSlider extends React.Component{
  render() {
    return (
      <div className="my-5">
        <label htmlFor="myRange1">Pixelation: {this.props.value}</label>
        <input
          type="range" min="0" max="30" className="slider" id="myRange1"
          onChange={this.props.onChange}
        />
      </div>
    );
  }
}

class ThresholdSlider extends React.Component{
  render() {
    return (
      <div className="my-5">
        <label htmlFor="myRange2">Threshold: {this.props.value}</label>
        <input
          type="range" min="0" max="30" className="slider" id="myRange2"
          onChange={this.props.onChange}
        />
      </div>
    );
  }
}

class DisplayImg extends React.Component{
  render(){
    return(
      <div>
        <img src={this.props.picture} />
      </div>
    )
  }
}

class SubmitButton extends React.Component{
  render(){
    return(
      <button onClick={() => this.props.onClick()}>Debug</button>
    )
  }
}

class Editor extends React.Component {
  constructor(props){
    super(props);
    this.state = {
      //values for pixelization and posterize alteration
      pixelValue: 0,
      thresholdValue: 0,
      //value that holds the path to default image, then the base64 data of render
      picture: src,
    };
    this.myRef = React.createRef();
    this.onDrop = this.onDrop.bind(this);
    this.jimpTest = this.jimpTest.bind(this);
  }


  handlePixelChange = (i) => {
    this.setState({pixelValue: i.target.value});
  }

  handleThresholdChange = (i) => {
    this.setState({thresholdValue: i.target.value});
  }

  renderPixelateSlider(){
    return (
      <PixelateSlider
        value={this.state.pixelValue}
        onChange={this.handlePixelChange}
      />
    );
  }

  renderThresholdSlider(){
    return (
      <ThresholdSlider
        value={this.state.thresholdValue}
        onChange={this.handleThresholdChange}
      />
    );
  }

  renderFinalImage(){
    console.log(this.state.picture);
    return (
      <div>
        <DisplayImg
          picture={this.state.picture}
        />
      </div>
    )
  }

  renderSubmitButton(i){
    return(
      <SubmitButton
        onClick={() => this.handleImageChange(i)}
      />
    )
  }


  jimpTest(path){
    console.log("jimpTest ran");
    console.log("path = " + path);
    jimp.read(path, function(err, image) {
      image.resize(800,10);
      image.getBase64(Jimp.MIME_PNG, function(err, data){
        console.log(data);
        this.setState({
          picture: data,
        });
      }.bind(this));
    }.bind(this));
  }

  onImageChange(i){
    console.log(this.state.picture);
  }

  onDrop(picture){
    //pull most recently uploaded picture from current session
    this.setState({
      picture: URL.createObjectURL(picture[picture.length - 1]),
    });
    //console.log(picture[picture.length - 1]);
    if (picture[picture.length - 1] != undefined){
      src = URL.createObjectURL(picture[picture.length - 1]);
    }
    console.log(this.jimpTest(URL.createObjectURL(picture[picture.length - 1])));
  }

  render() {
    return (
      <div>
        <div>
        </div>
        <div>
          {this.renderFinalImage()}
          {this.renderSubmitButton()}
        </div>
        <div>
          <ImageUploader
            withIcon={true}
            buttonText='Choose image'
            onChange={this.onDrop}
            imgExtension={['.jpg', '.gif', '.png']}
            maxFileSize={5242880}
          />
        </div>
        <div>
          {this.renderPixelateSlider()}
          {this.renderThresholdSlider()}
        </div>
      </div>
    );
  }

  //take "Pixelify" object, return div with pixelated image
  /*
  pixelateImage(i){
    if (i != undefined){
      imgFinal = i.canvas.toDataURL();
    }

    return (
      <div>
      <Pixelify
      src={src}
      centered={true}
      pixelSize={this.state.pixelValue}
      ref={this.myRef}
      />
      <div>{this.makeReference(this.myRef.current)}</div>
      </div>
    )
  }
  //helper function to create reference image within DOM element
  makeReference(i){
    if (i != undefined){
      //console.log(i);
      imgFinal = i.canvas.toDataURL();
    }
  }
  */
}

class App extends React.Component {
  render() {
    return (
      <div className="app">
        <div className="app-img">
          <Editor />
        </div>
        <div className="img-fields">
          <div>
          </div>
        </div>
      </div>
    );
  }
}

// ======================================
ReactDOM.render(
  <App />,
  document.getElementById('root')
);
